small {
  font-size: 100% !important;
  display: block;
  padding-top: 10px;
}

fieldset.MuiFormControl-root {
  padding-bottom: 0 !important;
  padding-top: 0.5em;
  width: 100%;
}

.MuiTypography-h6 {
  font-weight: 400 !important;
  margin-bottom: 0 !important;
}

span.required {
  color: #d93025;
}

p.error {
  margin-top: 0 !important;
  margin-bottom: 0 !important;
  color: #d93025;
}

.yes-or-no > button {
  position: relative;
  margin-top: 0 !important;
  margin-left: 5px !important;
}

.multiple-checkboxes-limited-height {
  max-height: 320px;
  overflow-x: scroll;
  border: 1px solid #bbb;
  margin-top: 10px;
}

.multiple-checkboxes-limited-height > div {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  padding-left: 20px;
  margin-right: 20px;
  max-height: 300px;
}

.multiple-checkboxes-limited-height::-webkit-scrollbar-track {
  background: rgba(0,0,0,0);
}

.multiple-checkboxes-limited-height::-webkit-scrollbar {
  width: 0.8em;
}

.multiple-checkboxes-limited-height::-webkit-scrollbar-thumb {
  background: transparent;
  border: solid 2px transparent;
  box-shadow: inset 0 0 10px 10px rgba(0, 0, 0, 0.25);
  border-radius: 16px;
}

img.preview {
  display: block;
  max-height: 300px;
}

.loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background:rgba(255, 255, 255, 0.85);
}

.loading-overlay > div {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  font-size: 1.4em;
}